body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

/* Original Style App Styling */
body {
  font-family: Cochin, Georgia, 'Times New Roman', serif;
  color: #333;
  background-color: white;
  margin: 0;
  padding: 0;
  line-height: 1.4;
}

.App {
  text-align: center;
  margin-top: 80px; /* Original margin from old CSS */
  height: calc(100vh - 80px);
  display: flex;
  flex-direction: column;
}

/* Navigation styling - based on original */
.nav-container {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background-color: white;
  display: flex;
  justify-content: center;
  padding: 10px 0;
  z-index: 1000;
}

.Links {
  font-family: Cochin, Georgia, 'Times New Roman', serif;
  padding: 0 40px; /* Proper spacing like original */
  font-size: 1em;
}

/* Content area */
.content-container {
  flex: 1 1;
  display: flex;
  flex-direction: column;
  overflow: auto;
}

/* Home page specific */
.home-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  padding-top: 20px;
  height: 100%;
  max-height: calc(100vh - 80px);
}

.chatbot-wrapper {
  width: 100%;
  max-width: 700px;
  margin: 20px auto;
  height: calc(100vh - 250px); /* Dynamically adjust height based on viewport */
  min-height: 300px;
  max-height: 500px;
}

/* Page containers */
.page-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: calc(100vh - 100px);
  padding: 10px;
}

/* CV route */
.CV-route {
  height: calc(100vh - 100px);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 10px;
}

/* Original header styles */
.App-header {
  font-family: Cochin, Georgia, 'Times New Roman', serif;
  height: auto;
  padding: 30px;
  margin-bottom: 0;
}

.App-title {
  font-size: 1.5em;
  margin-bottom: 10px;
}

.App-intro {
  font-family: Cochin, Georgia, 'Times New Roman', serif;
  font-size: large;
  margin-top: 0;
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .nav-container {
    flex-wrap: wrap;
  }
  
  .Links {
    padding: 10px 20px;
  }
  
  .chatbot-wrapper {
    height: calc(100vh - 230px);
  }
  
  .App-title {
    font-size: 1.3em;
    margin-bottom: 8px;
  }
}

@media (max-width: 480px) {
  .App {
    margin-top: 120px; /* More space for the wrapped navigation */
    height: calc(100vh - 120px);
  }
  
  .nav-container {
    padding: 5px 0;
    flex-wrap: wrap;
  }
  
  .Links {
    padding: 8px 15px;
    font-size: 0.9em;
  }
  
  .chatbot-wrapper {
    height: calc(100vh - 250px);
    min-height: 250px;
  }
  
  .App-header {
    padding: 15px 10px;
  }
  
  .App-intro {
    font-size: medium;
    display: block;
    margin: 10px 0;
  }
}

/* Simplified CV/Resume Component Styles */
.cv-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  max-width: 850px;
  margin: 0 auto;
  padding: 1rem;
  font-family: Cochin, Georgia, 'Times New Roman', serif;
  height: calc(100vh - 60px);
}

.cv-loading {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 300px;
  width: 100%;
  color: #666;
}

.cv-spinner {
  width: 30px;
  height: 30px;
  margin-bottom: 1rem;
  border: 2px solid rgba(0, 0, 0, 0.1);
  border-top: 2px solid #666;
  border-radius: 50%;
  -webkit-animation: spin 1s linear infinite;
          animation: spin 1s linear infinite;
}

@-webkit-keyframes spin {
  0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes spin {
  0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.cv-error {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 200px;
  width: 100%;
  color: #c00;
  background-color: #fff;
  border: 1px solid #eee;
  padding: 1rem;
  text-align: center;
}

.cv-document {
  width: 100%;
  height: auto;
  margin-bottom: 1rem;
  border: 1px solid #eee;
  background-color: white;
  display: flex;
  justify-content: center;
  overflow: auto;
}

.pdf-document {
  width: 100%;
  display: flex;
  justify-content: center;
}

.pdf-page {
  margin: 0;
  box-shadow: none !important;
}

.pdf-page canvas {
  margin: 0 auto !important;
  max-width: 100% !important;
  height: auto !important;
}

.cv-controls {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0.5rem;
  background-color: white;
  border: 1px solid #eee;
  margin-top: 0.5rem;
  flex-wrap: wrap;
  grid-gap: 1rem;
  gap: 1rem;
}

.cv-pagination {
  display: flex;
  align-items: center;
  grid-gap: 0.5rem;
  gap: 0.5rem;
}

.cv-page-info {
  font-size: 0.875rem;
  color: #333;
}

.cv-zoom {
  display: flex;
  align-items: center;
  grid-gap: 0.5rem;
  gap: 0.5rem;
}

.cv-zoom-level {
  font-size: 0.875rem;
  color: #333;
  min-width: 3rem;
  text-align: center;
}

.cv-button {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 28px;
  height: 28px;
  background-color: #f8f8f8;
  border: 1px solid #ddd;
  color: #333;
  cursor: pointer;
}

.cv-button:hover {
  background-color: #eee;
}

.cv-button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.cv-download-button {
  display: flex;
  align-items: center;
  grid-gap: 0.5rem;
  gap: 0.5rem;
  padding: 0.3rem 0.7rem;
  background-color: #f8f8f8;
  border: 1px solid #ddd;
  color: #333;
  font-size: 0.875rem;
  text-decoration: none;
}

.cv-download-button:hover {
  background-color: #eee;
}

@media (max-width: 768px) {
  .cv-container {
    padding: 0.5rem;
    height: calc(100vh - 50px);
  }
  
  .cv-controls {
    flex-direction: column;
    align-items: center;
    grid-gap: 0.5rem;
    gap: 0.5rem;
  }
  
  .cv-pagination, .cv-zoom {
    width: 100%;
    justify-content: center;
  }
}
/* Simplified About Component Styles */
