  max-width: 500px;
}

.About-header {
  font-family: Cochin, Georgia, 'Times New Roman', serif;
  height: auto;
  padding: 10px;
}

.About-title {
  font-size: 1.5em;
  font-weight: normal;
  margin-bottom: 10px;
}

.About-intro {
  font-family: Cochin, Georgia, 'Times New Roman', serif;
  font-size: large;
  line-height: 1.4;
  color: #333;
}

@media (max-width: 768px) {
  .About {
    padding-bottom: 15px;
  }
  
  .About-intro {
    font-size: medium;
  }
}

/* Simplified Contact Component Styles */
.Contact {
  text-align: center;
  max-width: 500px;
}

.Contact-header {
  font-family: Cochin, Georgia, 'Times New Roman', serif;
  height: auto;
  padding: 10px;
}

.Contact-title {
  font-size: 1.5em;
  font-weight: normal;
